import DataStore from 'flux/stores/DataStore.js'

class SecondaryPageBanner extends React.Component {
    render() {
         return (
            <div className="page-banner">
                <img src="http://api2.alexarauschernaturopath.com/api2/wp-content/uploads/2017/09/alexa-rauscher-banner.jpg" alt={this.props.pageName} className="page-banner__image"/>
                <h1 className="page-banner__title">{this.props.pageName}</h1>
            </div>

        );
    }
}

export default SecondaryPageBanner;



